@import url(../../fonts/Bungee/Bungee-Regular.css);
@import url(../../fonts/Orbitron/Orbitron-Regular.css);
@import url(../../fonts/Passero_One/PasseroOne-Regular.css);
#main {
  background: -owg-linear-gradient(120deg, rgba(192, 192, 192, 0.8), rgba(192, 192, 192, 0.9), rgba(192, 192, 192, 0.8));
  background: -webkit-linear-gradient(120deg, rgba(192, 192, 192, 0.8), rgba(192, 192, 192, 0.9), rgba(192, 192, 192, 0.8));
  background: -moz-linear-gradient(120deg, rgba(192, 192, 192, 0.8), rgba(192, 192, 192, 0.9), rgba(192, 192, 192, 0.8));
  background: -o-linear-gradient(120deg, rgba(192, 192, 192, 0.8), rgba(192, 192, 192, 0.9), rgba(192, 192, 192, 0.8));
  background: linear-gradient(120deg, rgba(192, 192, 192, 0.8), rgba(192, 192, 192, 0.9), rgba(192, 192, 192, 0.8)); }
  #main #content span, #main #content span:hover {
    font-family: PasseroOne-Regular, Arial, Helvetica Neue, Helvetica, sans-serif;
    font-size: 1.5em;
    color: #000000; }
  #main #content i.far {
    font-size: 0.5em !important; }
  #main #content a {
    text-decoration: none !important;
    color: #000000; }
  #main #content div.info {
    font-family: Orbitron-Regular, Arial, Helvetica Neue, Helvetica, sans-serif; }



