#data div.ctrl {
  background-color: #1d1d1d;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  padding-left: 5px;
  padding-bottom: 2px; }
  #data div.ctrl span {
    padding: 0 4.5px;
    font-size: 0.8em;
    font-family: PasseroOne-Regular, Bungee-Regular, AtomicAge-Regular, Arial, Helvetica Neue, Helvetica, sans-serif;
    cursor: pointer;
    color: #000;
    background-color: #c0c0c0;
    margin: 0 3px; }
  #data div.ctrl span.pl-1 {
    color: #ffffff;
    text-transform: uppercase;
    font-size: 0.8em;
    background-color: transparent !important; }

#data table.table tr {
  background-color: rgba(255, 255, 255, 0.55);
  border-bottom: 2px solid rgba(108, 117, 125, 0.8);
  cursor: pointer; }

#data table.table tr:last-child {
  border-bottom: 0; }

#data table.table thead tr {
  border-bottom: 2px solid rgba(108, 117, 125, 0.8) !important;
  cursor: auto; }

#data table.table th {
  color: #160521;
  text-transform: uppercase;
  font-family: Orbitron-Regular, Arial, Helvetica Neue, Helvetica, sans-serif;
  font-size: 0.8em;
  letter-spacing: 1px;
  border: 0 !important; }

#data table.table td {
  color: #000;
  font-family: PasseroOne-Regular, Arial, Helvetica Neue, Helvetica, sans-serif; }

#data #details table.table tr {
  cursor: auto; }
  #data #details table.table tr td a {
    color: #160521;
    text-decoration: underline; }
  #data #details table.table tr td a:hover {
    color: rgba(22, 5, 33, 0.6); }
  #data #details table.table tr td ul li {
    margin: 0;
    padding: 0; }



